import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { listWeatherAPI } from '../app/api/app_api'
import { connect } from 'react-redux'
import { Table, TableBody, TableCell, TableHead, TableRow, Paper } from '@material-ui/core';

class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      weatherResponse: [],
      city: 'Jakarta',
      error: '',
      initialLoading: true,
    }
  }

  async componentDidMount() {
    this.getWeather()
  }

  async getWeather() {
    try {
      const listWeather = await listWeatherAPI(this.state.city)
      if (listWeather.status !== 200) throw Error()
      this.setState({ weatherResponse: listWeather })
    } catch (e) {
      this.setState({ error: 'Terjadi kesalahan pada saat memuat data' })
    }
    this.setState({ initialLoading: false })
  }

  render() {
    console.log(this.state.weatherResponse)
    const { classes } = this.props;
    return (
      <Paper className={classes}>
      </Paper>
    )
  }
}

App.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  user: state.city,
})

export default connect(mapStateToProps)(App)
